import { getCookie } from "../utils/cookie";
import axiosInstance from "./axiosInstance";

const fetchProduct = (productId) => {
  return axiosInstance.get(`/getProductById/${productId}`);
};

const getAllVariants = async (params) => {
  const response = await axiosInstance.get("/getAllVariantsOnUser", { params });
  return response.data;
};

const fetchAllUtsavVariants = () => {
  return axiosInstance.get("/getAllUtsavProduct");
};

const getAllFeaturedVariants = () => {
  return axiosInstance.get("/getAllFeaturedProduct");
};

const getFeaturedVariantsBasedOnCategory = async (categoryId) => {
  const response = await axiosInstance.get(
    `/getFeaturedProductBasedOnCategory/${categoryId}`
  );
  return response.data.productList;
};

const getAllNewArrivals = () => {
  return axiosInstance.get("/getAllNewArrivals");
};

const getAllTopSellingProducts = async () => {
  const response = await axiosInstance.get("/getAllTopSellingProduct");
  return response.data;
};

const getVariant = async (variantId) => {
  const response = await axiosInstance.get(`/getVariantById/${variantId}`);
  return response.data?.variantDetails;
};

const getRecommendation = async (variantId) => {
  const response = await axiosInstance.get(`/getVariantById/${variantId}`);
  return response.data?.suggestionProductList;
};

const getAllReviews = (productId) => {
  return axiosInstance.get(`/GetAllReviews/${productId}`);
};

const getReviewById = (productId, reviewId) => {
  return axiosInstance.get(`/reviewByID/${productId}/${reviewId}`);
};

const getRatingDetails = (productId) => {
  return axiosInstance.get(`/getAvgRating/${productId}`);
};

const createReview = (reviewData, productId) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(`/createReview/${productId}`, reviewData, {
    headers: {
      authorization: `Bearer ${token}`,
      "Content-Type": "multipart/form-data",
    },
  });
};

const updateReview = (reviewData, productId, reviewId) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(
    `/updateReview/${productId}/${reviewId}`,
    reviewData,
    {
      headers: {
        authorization: `Bearer ${token}`,
      },
    }
  );
};

const deleteReview = (reviewId) => {
  return axiosInstance.get(`/deleteReview/${reviewId}`);
};

export {
  fetchProduct,
  getAllVariants,
  fetchAllUtsavVariants,
  getAllFeaturedVariants,
  getFeaturedVariantsBasedOnCategory,
  getAllNewArrivals,
  getAllTopSellingProducts,
  getVariant,
  getRecommendation,
  createReview,
  getAllReviews,
  getReviewById,
  getRatingDetails,
  updateReview,
  deleteReview,
};
