import axiosInstance from "./axiosInstance";

const getBanners = async (params) => {
  const response = await axiosInstance.get("/getAllBannersByBannerType", {
    params,
  });
  return response.data?.data;
};

const getAllGalleryBanners = () => {
  return axiosInstance.get("/getAllPublishedGallery");
};

export { getBanners, getAllGalleryBanners };
