import { useBanners } from "../services/bannerServices";
import Carousel from "./Carousel";

const HeroBanner = ({
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const { data: banners, isLoading } = useBanners({
    bannerType: "Hero",
    position,
    resourceType,
    valueId,
  });

  if (isLoading) {
    return <></>;
  }

  return (
    banners?.length > 0 && (
      <div
        className={`space-y-4 max-lg::space-y-2 max-md:pr-0 ${containerClassName}`}
      >
        {title || subtitle ? (
          <div>
            <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>
            <p className="text-sm">{subtitle}</p>
          </div>
        ) : null}
        <Carousel navigation autoplay delay={5000} allowTouchMove>
          {banners.map((banner, index) => (
            <a
              key={banner.bannerTitle || index}
              href={banner.linkUrl}
              className="block relative w-full h-max rounded-lg"
            >
              <img
                src={banner.bannerImg}
                alt={banner.bannerTitle || index}
                loading="lazy"
                className={`w-full object-cover object-center ${className}`}
              />
            </a>
          ))}
        </Carousel>
      </div>
    )
  );
};

export default HeroBanner;
