import { useCallback, useEffect, useState } from "react";
import {
  getAllReviews,
  getReviewById,
  createReview,
  updateReview,
  deleteReview,
  getRatingDetails,
} from "../api/productApis";
import { useUser } from "../contexts/userContext";
import { useToast } from "../contexts/toastContext";

const useReview = (productId, reviewId) => {
  const [reviews, setReviews] = useState([]);
  const [review, setReview] = useState(null);
  const [ratingDetails, setRatingDetails] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { user } = useUser();
  const toast = useToast();

  const fetchReviews = useCallback(async () => {
    if (!productId) return;
    setLoading(true);
    setError(null);

    try {
      const cacheKey = `reviews_${productId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setReviews(JSON.parse(cachedData));
      } else {
        const response = await getAllReviews(productId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(cacheKey, JSON.stringify(response.data));
          setReviews(response.data);
        }
      }
    } catch (error) {
      console.error("Error fetching reviews", error);
      setError("Failed to fetch reviews. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [productId]);

  const fetchReview = useCallback(async () => {
    if (!productId || !reviewId) return;
    setLoading(true);
    setError(null);

    try {
      const cacheKey = `review_${reviewId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setReview(JSON.parse(cachedData));
      } else {
        const response = await getReviewById(productId, reviewId);

        if (response?.status) {
          console.log(response.data);
          localStorage.setItem(cacheKey, JSON.stringify(response.data));
          setReview(response.data);
        }
      }
    } catch (error) {
      console.error("Error fetching review", error);
      setError("Failed to fetch review. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [productId, reviewId]);

  const fetchRatingDetails = useCallback(async () => {
    if (!productId) return;
    setLoading(true);
    setError(null);

    try {
      const cacheKey = `ratingDetails_${productId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setRatingDetails(JSON.parse(cachedData));
      } else {
        const response = await getRatingDetails(productId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(cacheKey, JSON.stringify(response.data));
          setRatingDetails(response.data);
        }
      }
    } catch (error) {
      console.error("Error fetching rating", error);
      setError("Failed to fetch rating. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [productId]);

  const addReview = useCallback(
    async (reviewData, productId) => {
      if (!user) {
        toast.error("You need to be logged in for posting any review.");
        return;
      }

      setLoading(true);
      setError(null);
      try {
        const response = await createReview(reviewData, productId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.removeItem(`reviews_${productId}`);
          sessionStorage.removeItem(`ratingDetails_${productId}`);
          fetchReviews();
        }
      } catch (error) {
        console.error("Error adding review", error);
        setError("Failed to add review. Please try again.");
      } finally {
        setLoading(false);
      }
    },
    // eslint-disable-next-line
    [user, fetchReviews]
  );

  const editReview = useCallback(
    async (reviewData, productId, reviewId) => {
      if (!user) {
        toast.error("You need to be logged in for editing any review.");
        return;
      }

      setLoading(true);
      setError(null);
      try {
        const response = await updateReview(reviewData, productId, reviewId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.removeItem(`reviews_${productId}`);
          sessionStorage.removeItem(`review_${reviewId}`);
          sessionStorage.removeItem(`ratingDetails_${productId}`);
          fetchReviews();
        }
      } catch (error) {
        console.error("Error editing review", error);
        setError("Failed to edit review. Please try again.");
      } finally {
        setLoading(false);
      }
    },
    // eslint-disable-next-line
    [user, fetchReviews]
  );

  const removeReview = useCallback(
    async (productId, reviewId) => {
      if (!user) {
        toast.error("You need to be logged in for editing any review.");
        return;
      }

      setLoading(true);
      setError(null);
      try {
        const response = await deleteReview(reviewId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.removeItem(`reviews_${productId}`);
          sessionStorage.removeItem(`review_${reviewId}`);
          sessionStorage.removeItem(`ratingDetails_${productId}`);
          fetchReviews();
        }
      } catch (error) {
        console.error("Error removing review", error);
        setError("Failed to remove review. Please try again.");
      } finally {
        setLoading(false);
      }
    },
    // eslint-disable-next-line
    [user, fetchReviews]
  );

  useEffect(() => {
    fetchReviews();
  }, [fetchReviews]);

  useEffect(() => {
    fetchReview();
  }, [fetchReview]);

  useEffect(() => {
    fetchRatingDetails();
  }, [fetchRatingDetails]);

  return {
    reviews,
    review,
    averageRating: ratingDetails?.averageRating?.toFixed(1),
    totalReviews: ratingDetails?.totalCount,
    ratingsBreakdown: ratingDetails?.ratingCounts,
    addReview,
    editReview,
    removeReview,
    loading,
    error,
  };
};

export default useReview;
